import React, {useState, useEffect} from 'react'
import Sidebar from '../../components/sidebar/Sidebar'
import Topbar from '../../components/topbar/Topbar'
import { endpoint } from '../../context/endpoint'
import './evenement.css'
import axios from 'axios'
import Rightbar from '../../components/rightbar/Rightbar'
import { Paper } from '@material-ui/core';
import {ScheduleOutlined} from '@material-ui/icons'

export default function Evenement() {
    const [event, setEvent] = useState([])
    const PF = process.env.REACT_APP_PUBLIC_FOLDER;
    const getEvent = async () => {
       await axios.get(`${endpoint}/api/evenements`)
       .then((data)=>{
            setEvent(data.data)
       })
    }
    useEffect(() => {
        getEvent();
    }, [event])
    return (
        <>
            <Topbar/>
            <div className='evenement'>
                <div className='sidebarevent'>
                    <Sidebar/>
                </div>
                <div className='evenementpage'>
                    <Paper elevation={3} style={{margin:20, padding: 20}}>
                        {event.map((event)=>(
                                <div className="card border-0">
                                    <div className="card-body">
                                        <h1 className="card-title" key={event.Titre}>{event.Titre}</h1>
                                        <p className="card-text" key={event.desc}>{event.desc}</p>
                                        <p className="card-text1" key={event.dateEvenement}> <ScheduleOutlined/>  {new Date(event.dateEvenement).toLocaleString()}</p>
                                    </div>
                                    <img style={{height:350, width:"100%", objectFit: "cover"}} key={event.image} src={`${endpoint}/images/${event.image}`} class="card-img-bottom" alt={event.Titre}/>
                                </div>
                        ))}
                </Paper>
                </div>
                <div className='rightevent'>
                    <Rightbar/>
                </div>
            </div>
        </>
    )
}
