import React, {useState, useEffect, useRef} from 'react'
import Sidebar from '../../components/sidebar/Sidebar'
import Topbar from '../../components/topbar/Topbar'
import { endpoint } from '../../context/endpoint'
import './livre.css'
import {Paper, Button} from '@material-ui/core'
import {Send} from '@material-ui/icons'
import axios from "axios"
import { AuthContext } from '../../context/AuthContext'
import { useContext } from 'react'

export default function Livre() {
    const [livres, setLivres] = useState([])
    const [file, setFile] = useState(null)
    const desc = useRef();
    const Titre = useRef();
    const { user } = useContext(AuthContext);
    const getAllBooks = async ()=>{
        await axios.get(`${endpoint}/api/livre`)
        .then((book)=>{
            setLivres(book.data);
        })
        .catch((err)=>{
            console.log(err)
        })
    }
    const submitHandler = async (e) => {
        e.preventDefault();
        const newPost = {
            userId: user._id,
            Titre: Titre.current.value,
            desc: desc.current.value
        };
        if (file) {
          const data = new FormData();
          const fileName = Date.now() + file.name;
          data.append("name", fileName);
          data.append("file", file);
          newPost.couverture = fileName;
          console.log(newPost);
          try {
            await axios.post(`${endpoint}/api/livre/upload`, data);
          } catch (err) {}
        }
        try {
          await axios.post(`${endpoint}/api/livre`, newPost);
          window.location.reload();
        } catch (err) {}
      };
    useEffect(() => {
        getAllBooks();
    }, [])
    return (
        <>
        <Topbar/>
        <div className='livre'>
            <div className='livresidebar'>
                <Sidebar/>
            </div>
            <div className='livrecorp'>
                <div className="container mb-3">
                <form onSubmit={submitHandler} className='needs-validation'>
                <h2>Publier un livre</h2>
                <Paper elevation={3} style={{padding: 20, marginBottom: 20}}>
                    <div className="row">
                        <div className="col-sm-6">
                            <div className="row">
                                    <label for="exampleInputEmail1" class="form-label col-sm-2">Titre</label>
                                    <div className="col-sm-10 mb-3">
                                        <input required ref={Titre} type="text" class="form-control" id="exampleInputEmail1" placeholder='Titre livre'/>
                                    </div>
                                    <label for="floatingTextarea" className='col-sm-2'>Description</label>
                                    <div className="col-sm-10">
                                        <textarea required ref={desc} class="form-control" placeholder="Description du livre" id="floatingTextarea"></textarea>
                                    </div>
                            </div>
                        </div>
                            <div className="col-sm-6">
                                <div className="row">
                                        <label for="exampleInputPassword1" className="form-label col-sm-2">Ressource du livre</label>
                                    <div class="col-sm-10 mb-3">
                                        <input required onChange={(e) => setFile(e.target.files[0])} type="file" class="form-control" id="exampleInputPassword1"/>
                                    </div>
                                    <div className="col-sm-12"> 
                                    <Button style={{
                                        backgroundColor:"#304CA6",
                                        color:"#FFF",
                                        marginTop: "20px"
                                    }} type="submit" variant="contained" startIcon={<Send/>}>Publier</Button>
                                    </div>
                                </div>
                            </div>
                    </div>
                </Paper>
                </form>
                </div>
                <div className="container">
                    <div className="row">
                            {livres.map((b)=>(
                             <div className="col-sm-4"> 
                                <div className="card">
                                    <img style={{height: "20rem", objectFit: "cover"}} src={`${endpoint}/images/${b.couverture}`} className="card-img-top" alt={b.couverture}/>
                                    <div className="card-body">
                                        <h5 className="card-title">{b.Titre}</h5>
                                        <p className="card-text">{b.desc}</p>
                                        <a href="https://www.xl6.com/articles/extraits-1/9782755001136.pdf" target="_blank" className="btn btn-primary">Extrait</a>
                                    </div>
                                </div>
                            </div>
                            ))}
                </div>
                </div>
            </div>
        </div>  
        </>
    )
}
