import React, {useState, useEffect, useRef, useContext} from 'react'
import Sidebar from '../../components/sidebar/Sidebar'
import Topbar from '../../components/topbar/Topbar'
import axios from 'axios'
import "./offres.css"
import { Button, Card } from '@material-ui/core'
import {CloudDownloadOutlined} from '@material-ui/icons'
import { AuthContext } from '../../context/AuthContext'
import { endpoint } from '../../context/endpoint'

export default function Offres() {
    const [offres, setOffres] = useState([]);
    const [file, setFile] = useState(null);
    const desc = useRef();
    const Titre = useRef();
    const { user } = useContext(AuthContext);
    const organisation = useRef();
    const dateExpiration = useRef(new Date)
    const submitHandler = async (e) => {
        e.preventDefault();
        const newOffre = {
            userId: user._id,
            Titre: Titre.current.value,
            desc: desc.current.value,
            organisation: organisation.current.value,
            dateExpiration: dateExpiration.current.value
        };
        if (file) {
          const data = new FormData();
          const fileName = Date.now() + file.name;
          data.append("name", fileName);
          data.append("file", file);
          newOffre.pdfLink = fileName;
          console.log(newOffre);
          try {
            await axios.post(`${endpoint}/api/livre/upload`, data);
          } catch (err) {}
        }
        try {
          await axios.post(`${endpoint}/api/offre/save`, newOffre);
          window.location.reload();
        } catch (err) {}
      };
    const getAllOffres = async()=>{
        await axios.get(`${endpoint}/api/offre`)
        .then((offre)=>{
            setOffres(offre.data)
        })
        .catch(e=>console.log(e))
    }
    useEffect(() => {
        getAllOffres();
        console.log(offres)
    }, [])

    return (
        <>
            <Topbar/>
            <div className='offres'>
                <div className='sidebaroffre'>
                    <Sidebar/>
                </div>
                <div className='offrecorp'>
                    <div className="container">
                        <Card elevation={3} style={{padding: 20, marginBottom: 20}}>
                            <div className="sendoffre">
                                <form onSubmit={submitHandler}>
                                    <div className="row">
                                        <div className="col-sm-6">
                                            <div className="row">
                                                <label htmlFor="organisation" className='col-sm-3 col-form-label'>Organisation</label>
                                                <div class="col-sm-9">
                                                    <input type="text" ref={organisation}  id='organisation' className='form-control form-control-sm mb-3'/>
                                                </div>
                                                <label htmlFor="poste" className='col-sm-3 col-form-label'>Poste</label>
                                                <div class="col-sm-9">
                                                    <input type="text" ref={Titre} id='poste' className='form-control form-control-sm mb-3' />
                                                </div>
                                                <label htmlFor="desc"   className='col-sm-3 col-form-label'>Description</label>
                                                <div class="col-sm-9">
                                                    <input type="text" ref={desc} id='desc' className='form-control form-control-sm mb-3'/>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-sm-6">
                                            <div className="row">
                                                <label htmlFor="dateExpiration"  className='col-sm-3 col-form-label'>Expire</label>
                                                <div class="col-sm-9">
                                                    <input type="date" ref={dateExpiration} id='dateExpiration' className='form-control form-control-sm mb-3'/>
                                                </div>
                                                <label htmlFor="ressources" className='col-sm-3 col-form-label'>Ressource</label>
                                                <div class="col-sm-9">
                                                    <input type="file" id='ressources' className='form-control form-control-sm mb-3' onChange={(e) => setFile(e.target.files[0])}/>
                                                </div>
                                                <div className="col-sm-9 align-items-end">
                                                    <button type='submit' className='btn' style={{width: 150, backgroundColor:"darkblue", color:"white"}}>Publier</button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </Card>
                    </div>
                    <div className="container">
                        <table className='table table-hover caption-top'>
                            <caption>Offres d'Emplois</caption>
                            <thead>
                                <tr>
                                    <th scope="col">Description</th>
                                    <th scope="col">Poste</th>
                                    <th scope="col">Organisation</th>
                                    <th scope="col">Expire</th>
                                    <th scope='col'>Ressource</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    offres.map((o)=>(
                                        <tr>
                                            <td>{o.desc}</td>
                                            <td>{o.Titre}</td>
                                            <th><td>{o.organisation}</td></th>
                                            <td style={{color:"green", fontSize:20, fontWeight:600}}>{new Date(o.dateExpiration).toLocaleDateString()}</td>
                                            <td><a style={{textDecoration:"none", cursor:"pointer"}} href={`${endpoint}/images/${o.pdfLink}`}
                                            target="_blank" rel='noreferrer'
                                            >
                                                <Button style={{color:"orange"}} startIcon={<CloudDownloadOutlined/>}>Download</Button>
                                                </a></td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </>
    )
}

 
