import React, {useContext, useState, useEffect, useRef} from 'react'
import Topbar from '../../components/topbar/Topbar';
import Conversations from '../conversations/Conversations';
import "./messenger.css";
import Message from '../message/Message';
import ChatOnline from '../../components/chatOnline/ChatOnline';
import {AuthContext} from '../../context/AuthContext';
import { endpoint } from '../../context/endpoint'
import axios from 'axios';
import {io} from 'socket.io-client';
export default function Messenger() {
    const {user} = useContext(AuthContext);
    const [conversations, setConversations]= useState([]);
    const [currentChat, setCurrentChat] = useState(null);
    const [newMessage, setNewMessage] = useState("");
    const [arrivalMessage, setArrivalMessag] = useState(null);
    const [messages, setMessages] = useState([]);
    const [onlineUsers, setOnlineUsers]= useState([]);
    const scrollRef = useRef()
    const socket = useRef()

    useEffect(() => {
        socket.current = io("ws://localhost:8900");
        socket.current.on("getMessage", data =>{
            setArrivalMessag({
                sender: data.senderId,
                text: data.text,
                createdAt: Date.now()
            })
        })
    }, []);

    useEffect(() => {
        arrivalMessage && currentChat?.members.includes(arrivalMessage.sender) &&
        setMessages((prev)=>[...prev, arrivalMessage]);
    }, [arrivalMessage, currentChat])

    useEffect(() => {
        socket.current.emit("AddUser", user._id);
        socket.current.on("getUsers", (users)=>{
            setOnlineUsers(user.followings.filter(f=>users.some((u)=>u.userId === f)));
        })
    }, [user]);

    useEffect(()=>{
        const getConversations = async ()=>{
        try{
            const res = await axios.get(`${endpoint}/api/conversations/${user._id}`)
            setConversations(res.data)
        }catch (err) {
            console.log(err);
        }
        }
        getConversations();
    }, [user._id]);

    useEffect(() => {
        const getMessage = async () => {
            try {
                const res = await axios.get(`${endpoint}/api/message/${currentChat._id}`)
                setMessages(res.data)
            } catch (err) {
                console.log(err)
            }
        }
        getMessage();
    }, [currentChat]);
    const handleSubmit= async(e)=>{
        e.preventDefault();
        const message = {
            sender: user._id,
            text: newMessage,
            conversationId: currentChat._id
        }
        const receiverId = currentChat.members.find(member=> member !== user._id);
        socket.current.emit("sendMessage", {
            senderId: user._id,
            receiverId,
            text: newMessage
        })
        try {
            const res = await axios.post(`${endpoint}/api/message`, message)
            setMessages([...messages, res.data]);
            setNewMessage("")
        } catch (err) {
            console.log(err)
        }
    }

    useEffect(() => {
        scrollRef.current?.scrollIntoView({behavior: "smooth"})
    }, [messages])
    return (
        <>
            <Topbar/>
            <div className="messenger">
                <div className="chatMenu">
                    <div className="chatMenuWrapper">
                        <input placeholder="Recherche d'amis " className="chatMenuInput"/>
                        {conversations.map((c)=>(
                            <div onClick={()=>setCurrentChat(c)}>
                                <Conversations conversation={c} currentUser={user}/>
                            </div>
                         ))
                        }
                    </div>
                </div>
                <div className="chatBox">
                    <div className="chatBoxWrapper">
                    {
                        currentChat ?
                        <>
                        <div className="chatBoxTop">
                            {
                                messages.map((m)=>(
                                    <div ref={scrollRef}>
                                        <Message message={m} own={m.sender === user._id}/>
                                    </div>
                                ))
                            }
                        </div>
                        <div className="chatBoxBottom">
                            <textarea className="chatMessageInput" placeholder="Write something ..."
                             onChange={e=> setNewMessage(e.target.value)} value={newMessage}
                            ></textarea>
                            <button className="chatSubmitButton" onClick={handleSubmit}>Envoyer</button>
                        </div></> : <span className="noConversationText">Ouvrez une conversation pour commencer le chat</span>}
                    </div>
                </div>
                <div className="chatOnline">
                    <div className="chatOnlineWrapper">
                        <ChatOnline onlineUsers={onlineUsers} currentId={user._id} setCurrentChat={setCurrentChat}/>
                    </div>
                </div>
            </div>
        </>
    )
}