import React, {useState, useEffect} from 'react';
import './chatOnline.css';
import logo from '../../logo.svg';
import axios from 'axios';

function ChatOnline({onlineUsers, currentId, setCurrentChat}) {
    const [friends, setFriends] = useState([])
    const [onlineFriends, setOnlineFriends] = useState([])

    useEffect(() => {
        const getFriends = async()=>{
            const res = await axios.get("http://localhost:8800/api/user/friends/"+currentId);
            setFriends(res.data);
        }
        getFriends();
    }, [currentId]);

    useEffect(() => {
        setOnlineFriends(friends.filter((f)=> onlineUsers.includes(f._id)))
    }, [friends, onlineUsers]);
    const handleClick =  async(user)=>{
        try {
            const res = await axios.get(`http://localhost:8800/api/conversations/find/${currentId}/${user._id}`);
            setCurrentChat(res.data);
        } catch (error) {
            console.log(error)
        }
    }
    return (
        <div className="chatOnline">
        {onlineFriends.map((o)=>(
            <div className="chatOnlineFriend" onClick={()=>{
                handleClick(o)
            }}>
                <div className="chatOnlineImgContainer">
                    <img className="chatOnlineImg" src={logo} alt="" />
                    <div className="chatOnlineBage"></div>
                </div>
                <span className="chatOnlineName">{o.username}</span>
            </div>
        ))}
        </div>
    )
}

export default ChatOnline
