import axios from "axios";
import { useRef } from "react";
import "./register.css";
import { useHistory } from "react-router";
import {Link} from "react-router-dom"
import { endpoint } from "../../context/endpoint";

export default function Register() {
  const username = useRef();
  const email = useRef();
  const password = useRef();
  const passwordAgain = useRef();
  const history = useHistory();
  const handleClick = async (e) => {
    e.preventDefault();
    if (passwordAgain.current.value !== password.current.value) {
      passwordAgain.current.setCustomValidity("Passwords don't match!");
    } else {
      const user = {
        username: username.current.value,
        email: email.current.value,
        password: password.current.value,
      };
    console.log(username,
email,
password,
passwordAgain)
      try {
        await axios.post(`${endpoint}/auth/register`, user);
        history.push("/login");
      } catch (err) {
        console.log(err);
      }
    }
  };

  return (
    <div className="login">
      <div className="loginWrapper">
        <div className="loginLeft">
          <h3 className="loginLogo">Anciens ULPGL</h3>
          <span className="loginDesc">
            Connect with friends and the world around you on Anciens ULPGL.
          </span>
        </div>
        <div className="loginRight">
          <form className="loginBox" onSubmit={handleClick}>
            <input
              placeholder="Username"
              required
              ref={username}
              className="loginInput"
              type="text"
            />
            <input
              placeholder="Matricule"
              required
              ref={email}
              className="loginInput"
              type="email"
            />
            <input
              placeholder="Password"
              required
              ref={password}
              className="loginInput"
              type="password"
              minLength="6"
            />
            <input
              placeholder="Password Again"
              required
              ref={passwordAgain}
              className="loginInput"
              type="password"
            />
            <button className="loginButton" type="submit" onClick={e=>console.log(handleClick(e))}>
              Sign Up
            </button>
            <Link to="/login">
              <button className="loginRegisterButton" >Log into Account</button>
            </Link>
          </form>
        </div>
      </div>
    </div>
  );
}
