import 'bootstrap/dist/css/bootstrap.min.css';
import Home from "./pages/home/Home";
import Login from "./pages/login/Login";
import Profile from "./pages/profile/Profile";
import Register from "./pages/register/Register";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { useContext } from "react";
import { AuthContext } from "./context/AuthContext";
import Messenger from "./pages/messenger/Messenger";
import Evenement from "./pages/evenement/Evenement";
import Offres from "./pages/offres/Offres";
import Livre from './pages/livres/Livre';

function App() {
  const { user } = useContext(AuthContext);
  return (
    <Router>
      <Switch>
        <Route exact path="/">
          {user ? <Home /> : <Register />}
        </Route>
        <Route exact path="/login">{user ? <Redirect to="/" /> : <Login />}</Route>
        <Route exact path="/register">
          {user ? <Redirect to="/" /> : <Register />}
        </Route>
        <Route exact path="/messenger">
          {!user ? <Redirect to="/" /> : <Messenger />}
        </Route>
        <Route exact path="/profile/:username">
          <Profile />
        </Route>
        <Route exact path="/livre">{!user ? <Redirect to="/" /> : <Livre />}</Route>
        <Route exact path="/offres">{!user ? <Redirect to="/" /> : <Offres />}</Route>
        <Route exact path="/evenement">{!user ? <Redirect to="/" /> : <Evenement />}</Route>
      </Switch>
    </Router>
  );
}

export default App;
